// ÂØπaxiosËøõË°å‰∫åÊ¨°Â∞ÅË£Ö

import axios from "axios"

// 1.Âà©Áî®axiosÊñπÊ≥ïcreateÔºåÂéªÂàõÂª∫‰∏Ä‰∏™axiosÂÆû‰æã
// 2.requestÂ∞±ÊòØaxiosÔºå‰∏∫‰∫ÜÈÖçÁΩÆ‰∏Ä‰∏ãaxios
const requests = axios.create({
  // Âü∫Á°ÄË∑ØÂæÑÔºåÂèëËØ∑Ê±ÇË∑ØÂæÑ‰∏≠‰ºöÂá∫Áé∞/api
  baseURL:"/api",
  // ‰ª£Ë°®ËØ∑Ê±ÇË∂ÖÊó∂‰∫ã‰ª∂5s
  timeout:5000,
});

// ËØ∑Ê±ÇÊã¶Êà™Âô®
requests.interceptors.request.use((config) => {
  // configÈÖçÁΩÆÂØπË±°ÔºåÂØπË±°ÈáåÊúâ‰∏Ä‰∏™Â±ûÊÄßÔºöheadersËØ∑Ê±ÇÂ§¥
  return config;
})

// ÂìçÂ∫îÊã¶Êà™Âô®
requests.interceptors.response.use((res) => {
  // ÊàêÂäüÂõûË∞ÉÂáΩÊï∞
  return res.data
},(error) => {
  // Â§±Ë¥•ÂõûË∞ÉÂáΩÊï∞
  return Promise.reject(new Error("fail"))
})

// ÂØπÂ§ñÊö¥Èú≤
export default requests;