              placeholder="ËØ∑ËæìÂÖ•‰Ω†ÁöÑÂßìÂêç"
              prefix-icon="el-icon-user-solid"
              clearable>
            </el-input>
          </el-form-item>

          <!-- Â≠¶Âè∑ -->
          <el-form-item label="" prop="studentNum">
            <el-input 
              v-model.number="registerForm.studentNum" 
              autocomplete="off" 
              placeholder="ËØ∑ËæìÂÖ•‰Ω†ÁöÑÂ≠¶Âè∑"
