<script>
export default {
  name: 'App-Register',
  data() {
    let validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('ËØ∑ËæìÂÖ•ÂØÜÁ†Å'));
      } else {
        if (this.registerForm.checkPass !== '') {
          this.$refs.registerForm.validateField('checkPass');
        }
        callback();
      }
    };
    let validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('ËØ∑ÂÜçÊ¨°ËæìÂÖ•ÂØÜÁ†Å'));
      } else if (value !== this.registerForm.pass) {
        callback(new Error('‰∏§Ê¨°ËæìÂÖ•ÂØÜÁ†Å‰∏ç‰∏ÄËá¥!'));
      } else {
        callback();
      }
    };
    return {
      registerForm: {
        userName: '',   // Ë¥¶Âè∑
        pass: '',       // ÂØÜÁ†Å
        checkPass: '',
        peopleName:'',  // ÂßìÂêç
        studentNum:'',  // Â≠¶Âè∑
        email:'',       // ÈÇÆÁÆ±
      },
      rules: {
        userName: [
          { required: true, message: 'ËØ∑ËæìÂÖ•Ë¥¶Âè∑', trigger: 'blur' },
          { min: 2, max: 9, message: 'ÈïøÂ∫¶2~9‰∏™Â≠óÁ¨¶', trigger: 'blur'}
        ],
        pass: [
          { validator: validatePass, trigger: 'blur' }
        ],
        checkPass: [
          { validator: validatePass2, trigger: 'blur' }
        ],
        peopleName: [
          { required: true, message: 'ËØ∑ËæìÂÖ•ÂßìÂêç', trigger: 'blur' }
        ],
        studentNum: [
          { required: true, message: 'ËØ∑ËæìÂÖ•Â≠¶Âè∑', trigger: 'blur' },
          { min: 15, max: 15, message: 'ÈïøÂ∫¶15‰∏™Êï∞Â≠ó', trigger: 'blur'}
        ],
        email: [
          { required: true, message: 'ËØ∑ËæìÂÖ•ÈÇÆÁÆ±Âú∞ÂùÄ', trigger: 'blur' }
        ]
      },
    };
  },
  methods: {
    // Ê≥®ÂÜåÊåâÈíÆ
    Register() {
      this.axios.post("http://localhost:xxxx/user/register",this.registerForm).then((response) => {
        let data = response.data
        if(data.success){
          this.registerForm = {}
          this.$router.push({path:"/Login"})
          this.$message({
            showClose:true,
            type:"success",
            message:"Ê≥®ÂÜåÊàêÂäüÔºåÂâçÂæÄÁôªÂΩï",
          })
        }
      })
      
    },

    // ËøîÂõûÁôªÂΩïÁïåÈù¢
    toLogin(){
      this.$router.push({path:"/Login"})
    }
  }
};
</script>

